.companyInfo {
  padding: 30px;
  pointer-events: auto;
  background: #ffc960; }
  .companyInfo ul {
    margin-top: 50px; }
    .companyInfo ul li {
      position: relative;
      padding-left: 40px;
      margin-bottom: 20px;
      color: #fff; }
      .companyInfo ul li a {
        color: #fff; }
        .companyInfo ul li a:hover {
          color: #fff; }
