.mastHead {
  width: 100%;
  height: 70px;
  position: absolute;
  top: 0;
  z-index: 1000;
  left: 0;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2); }
  .mastHead .container {
    position: relative; }
  .mastHead.fixed {
    z-index: 1001;
    top: 0;
    left: 0;
    right: 0;
    height: 60px;
    position: fixed;
    background: rgba(11, 25, 45, 0.93);
    border-bottom: none;
    -webkit-transition: all 0.2s ease-out;
    -o-transition: all 0.2s ease-out;
    transition: all 0.2s ease-out;
    /* menu appearance if header is sticky */ }
    .mastHead.fixed .navList li {
      height: 60px; }
      .mastHead.fixed .navList li a {
        height: 60px;
        line-height: 60px; }
    .mastHead.fixed .navList ul li, .mastHead.fixed .navList ul li a {
      height: auto;
      line-height: normal; }
  .mastHead .container, .mastHead .row, .mastHead .logo-parent {
    height: inherit; }
  .mastHead .logo {
    display: table;
    height: inherit; }
    .mastHead .logo a {
      display: table-cell;
      vertical-align: middle; }
    .mastHead .logo img {
      width: 100px; }
  .mastHead .mobileNavBtn {
    display: block;
    width: 40px;
    height: 40px;
    color: #ffffff;
    position: absolute;
    top: 18px;
    right: 15px;
    cursor: pointer;
    z-index: 100; }



